<template>
  <baseTable :fields="fields" baseURL="/api/bugLevel"></baseTable>
</template>

<script>
export default {

  name: 'bugLevelTable',

  data () {
    return {

      fields: [
        {
          field: 'bugLevelId',
          name: 'ID',
          hidden: true
        },
        {
          field: 'bugLevelName',
          name: ''
        },
        {
          field: 'createTime',
          name: '',
          type: 'date'
        },
        {
          field: 'createUserId',
          name: ''
        },
        {
          field: 'updateTime',
          name: '',
          type: 'date'
        },
        {
          field: 'updateUserId',
          name: ''
        },
        {
          field: 'remark',
          name: ''
        }
      ]
    }
  },

  methods: {}
}

</script>
<style scoped>
</style>



