<template>
  <baseTable :fields="fields" baseURL="/api/taskAttr" :btns="btns"></baseTable>
</template>

<script>
export default {

  name: 'taskAttrTable',

  data () {
    return {

      fields: [
        {
          field: 'taskAttrId',
          name: 'ID'
        },
        {
          field: 'taskId',
          name: 'ID'
        },
        {
          field: 'taskAttrKey',
          name: ''
        },
        {
          field: 'taskAttrValue',
          name: ''
        },
        {
          field: 'createTime',
          name: '',
          type: 'date'
        },
        {
          field: 'createUserId',
          name: ''
        },
        {
          field: 'updateTime',
          name: '',
          type: 'date'
        },
        {
          field: 'updateUserId',
          name: ''
        },
        {
          field: 'remark',
          name: ''
        }
      ],

      btns: [
        {
          title: '',
          icon: 'icon-shezhi',
          click: () => {
            this.$alert('', '', {
              confirmButtonText: ''
            })
          }
        }
      ]
    }
  },

  methods: {}
}

</script>
<style scoped>
</style>



