<template>
  <div>
    <baseTable :fields="fields" :baseURL="baseURL" :btns="btns"></baseTable>
    <el-dialog title="" width="95%" :visible.sync="taskInfo.show">
      <TaskInfoPage></TaskInfoPage>
    </el-dialog>
  </div>
</template>

<script>
import TaskInfoPage from './TaskInfoPage'
export default {

  name: 'taskTable',

  data () {
    return {

      taskInfo: {
        show: false
      },

      baseURL: '/api/task',

      fields: [
        {
          field: 'taskId',
          name: 'ID'
        },
        {
          field: 'taskName',
          name: ''
        },
        {
          field: 'desc',
          name: ''
        },
        {
          field: 'createTime',
          name: '',
          type: 'date'
        },
        {
          field: 'createUserName',
          name: ''
        },
        {
          field: 'updateTime',
          name: '',
          type: 'date'
        },
        {
          field: 'updateUserName',
          name: ''
        },
        {
          field: 'remark',
          name: ''
        }
        // {
        //   field: 'status',
        //   name: '',
        //   type: 'select',
        //   codeType: 'taskStatus',
        //   formatter (val) {
        //     return '<span class="tag">' + val + '</span>'
        //   }
        // }
      ],

      dialog: null,

      taskLogQuery: null,

      btns: [
        {
          title: '',
          icon: 'icon-addNew',
          click: () => {
            this.$router.push('/task/task-create')
          }
        },
        {
          title: '',
          icon: 'icon-guanlian',
          click: () => {
            this.$alert('', '', {
              confirmButtonText: ''
            })
          }
        },
        {
          title: '',
          icon: 'icon-guanlian',
          click: () => {
            this.$message({
              message: '',
              showClose: true,
              type: 'warning'
            })
          }
        },
        {
          title: '',
          icon: 'icon-xiangqing',
          click: (rows, handle) => {
            this.taskInfo.show = true
          }
        },
        {
          title: '',
          icon: 'icon-saomiao',
          click: (rows, handle) => {
            // this.dialog = this.$dialog({
            //   text: '!?',
            //   ok: this.startTaskOk
            // })

            this.$confirm('!?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'success'
            }).then(() => {
              this.startTaskOk()
            }).catch(() => {
              this.$message({
                type: 'info',
                message: ''
              })
            })

            if (rows.length === 1) {
              const row = rows[0]
              this
                .$post(this.baseURL + '/startTask', row)
                .then(data => {
                  const retData = data.data
                  if (data.success) {
                    this.taskLogQuery = {
                      taskLogId: retData.taskLogId
                    }
                    this.dialog.display()
                  } else {
                    this.$dialog({
                      text: ''
                    }).display()
                  }
                })
            } else {
              this.$message({
                message: '',
                showClose: true,
                type: 'warning'
              })
            }
          }
        }
      ]
    }
  },

  methods: {

    startTaskOk () {
      this.$router.push({
        path: '/task/task-log-table',
        query: this.taskLogQuery
      })
    }

  },

  components: {
    TaskInfoPage
  }
}

</script>
<style scoped>
</style>



