<template>
  <div>
    <baseTable :fields="fields" baseURL="/api/bug" :btns="btns"></baseTable>
    <!-- <Modal :show.sync="linkScript.isShow">
      <h1 slot="header"></h1>
    </Modal> -->

    <el-dialog
      title=""
      :visible.sync="linkScript.isShow"
      >
      <span>
        <el-transfer
          filter-placeholder=""
          :titles="['', '']"
          v-model="linkScript.value"
          :data="linkScript.data">
        </el-transfer>
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="linkScript.isShow = false"> </el-button>
        <el-button @click="linkScript.isShow = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {

  name: 'bugTable',

  data () {
    return {

      fields: [
        {
          field: 'bugId',
          name: 'ID',
          hidden: true
        },
        {
          field: 'bugName',
          name: ''
        },
        {
          field: 'desc',
          name: '',
          type: 'textarea'
        },
        {
          field: 'createTime',
          name: '',
          type: 'date'
        },
        {
          field: 'createUserName',
          name: ''
        },
        {
          field: 'updateTime',
          name: '',
          type: 'date'
        },
        {
          field: 'updateUserName',
          name: ''
        },
        {
          field: 'remark',
          name: '',
          type: 'textarea'
        }
      ],

      linkScript: {
        isShow: false,
        data: [{
          key: 1,
          label: '1'
        },
        {
          key: 2,
          label: '2'
        },
        {
          key: 3,
          label: '3'
        }],
        value: [1, 2]
      },

      btns: [
        {
          title: '',
          icon: 'icon-guanlian',
          click: (rows, handle) => {
            this.linkScript.isShow = true
            console.log(rows)
            console.log(handle)
          }
        },
        {
          title: '',
          icon: 'icon-guanlian',
          click: function (rows, handle) {
            console.log(rows)
            console.log(handle)
          }
        },
        {
          title: '',
          click: function (rows, handle) {
            console.log(rows)
            console.log(handle)
          }
        },
        {
          title: '',
          click: function (rows, handle) {
            console.log(rows)
            console.log(handle)
          }
        }
      ]
    }
  },

  methods: {}
}

</script>
<style scoped>
</style>



