<template>
  <div>
    <div class="panel">
      <h1></h1>

      <div class="panel__body">
        <form class="form">
          <div class="form__block">
            <label class="input-label" for=""></label>
            <input class="input" placeholder="" />
            <label class="add-btn" @click="isModal = true">+</label>
          </div>
          <div class="form__block">
            <label class="input-label" for=""></label>
            <input class="input" placeholder="" />
            <label class="add-btn" @click="isModal = true">+</label>
          </div>
          <!-- <div class="form__block">
            <label class="input-label" for=""></label>
            <textarea placeholder="" rows="5" style="resize:none; width: 30%; padding: 8px;"></textarea>
          </div>-->
          <div class="form__block">
            <el-button type="primary"></el-button>
            <el-button type="primary"  @click="resetForm"></el-button>
          </div>
        </form>
      </div>
    </div>

    <div class="modal" v-show="isModal">
      <div class="modal__inner panel">
        <h1></h1>
        <form class="form modal__body">
          <div :key="index" v-for="(attr, index) in attrs" class="form__block">
            <label class="input-label" for=""></label>
            <input class="input" v-model="attr.key" placeholder="" />
            <input class="input" v-model="attr.value" placeholder="" />
            <label class="add-btn" @click="delAttr(index)">-</label>
          </div>

          <div class="form__block">
            <label class="add-btn" @click="addAttr">+</label>
          </div>
          <div class="button-group">
            <a class="button"></a>
            <a class="button" @click="resetAattrs"></a>
            <a @click="isModal = false" class="button"></a>
          </div>
        </form>
      </div>
      <div class="mask"></div>
    </div>

    <div class="panel">
      <h1></h1>

      <div class="panel__body">
        <form class="form">
          <div class="form__block">
            <label class="input-label" for=""></label>
            <input class="input" placeholder="" />
          </div>
          <div class="form__block">
            <label style="margin-bottom: 16px;" class="input-label" for=""></label>
            <textarea
              style="width: 80%; height: 300px;"
              class="input"
              v-model="scriptContent"
              placeholder="..."
            ></textarea>
          </div>
          <div class="form__block">
            <a class="button"></a>
            <a class="button"></a>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'scriptPage',
  data () {
    return {
      isModal: false,
      attrs: [{
        key: '',
        value: ''
      }],
      scriptContent: ''
    }
  },

  methods: {
    addAttr () {
      this.attrs.push({
        key: '',
        value: ''
      })
    },

    resetAattrs () {
      this.attrs = [
        {
          key: '',
          value: ''
        }
      ]
    },

    delAttr (index) {
      if (this.attrs.length > 1) {
        this.attrs.splice(index, 1)
      }
    },

    resetForm () {

    }
  }
}

</script>
<style scoped>
.add-btn {
  display: inline-block;
  color: #666;
  border: 1px solid #c4c6cf;
  height: 30px;
  line-height: 30px;
  padding: 0 8px;
  cursor: pointer;
}

.add-btn:hover {
  background: rgba(0, 0, 0, 0.1);
  border-color: rgba(0, 0, 0, 0.1);
  color: #ffffff;
}
</style>



