<template>
  <div>
    <baseTable :fields="fields" baseURL="/api/asset" :btns="btns"></baseTable>

    <Modal :isEsc="true" :show.sync="assetInfo.show" title="" width="90%" height="600px">
      <AssetInfo></AssetInfo>
    </Modal>

    <Modal :show.sync="portScan.show">
      
    </Modal>

    <Modal :show.sync="assetDiscovery.show">
      <ul class="list">
        <li>1&nbsp;http://qq.com/a.html</li>
        <li>1&nbsp;http://qq.com/a.html</li>
        <li>1&nbsp;http://qq.com/a.html</li>
        <li>1&nbsp;http://qq.com/a.html</li>
        <li>1&nbsp;http://qq.com/a.html</li>
        <li>1&nbsp;http://qq.com/a.html</li>
        <li>1&nbsp;http://qq.com/a.html</li>
      </ul>
    </Modal>

  </div>
</template>

<script>
import AssetInfo from './AssetInfo'
export default {

  name: 'assetTable',

  data () {
    return {

      // 
      assetInfo: {
        show: false
      },

      // 
      portScan: {
        show: false
      },

      // 
      assetDiscovery: {
        show: false
      },

      fields: [
        {
          field: 'assetId',
          name: 'ID',
          hidden: true
        },
        {
          field: 'assetName',
          name: ''
        },
        {
          field: 'updateTime',
          name: '',
          type: 'date'
        },
        {
          field: 'createUserId',
          name: '',
          hidden: true
        },
        {
          field: 'createUserName',
          name: ''
        },
        {
          field: 'createTime',
          name: '',
          type: 'date'
        },
        {
          field: 'updateUserId',
          name: '',
          hidden: true
        },
        {
          field: 'updateUserName',
          name: ''
        },
        {
          field: 'remark',
          name: ''
        }
      ],

      btns: [
        {
          title: '',
          icon: 'icon-chazhao',
          click: () => {
            this.assetInfo.show = true
          }
        },
        {
          title: '',
          icon: 'icon-chazhao',
          click: () => {
            this.portScan.show = true
          }
        },
        {
          title: '',
          icon: 'icon-chazhao',
          click: () => {
            this.assetDiscovery.show = true
          }
        }
      ]
    }
  },

  components: {
    AssetInfo
  }
}

</script>
<style scoped>
</style>



