<template>
  <div>
    <div class="row">
      <div class="col-6">
        <el-card shadow="never" style="min-height: 600px;">
          <div slot="header" class="clearfix">
            <span></span>
          </div>
          <el-form :model="form" label-width="80px" size="small">
            <el-form-item label="">
              <el-row :gutter="12">
                <el-col :span="12">
                  <el-input v-model="form.taskName" placeholder=""></el-input>
                </el-col>
                <el-col :span="4">
                  <el-link type="primary" @click="generateTaskName"></el-link>
                </el-col>
              </el-row>
            </el-form-item>
            <el-form-item label="">
              <el-row :gutter="12">
                <el-col :span="12">
                  <el-select v-model="form.cycle" clearable>
                    <el-option value="0" label=""></el-option>
                    <el-option value="1" label=""></el-option>
                    <el-option value="2" label=""></el-option>
                    <el-option value="3" label=""></el-option>
                    <el-option value="4" label=""></el-option>
                  </el-select>
                </el-col>
              </el-row>
            </el-form-item>
            <el-form-item label="">
              <el-row :gutter="12">
                <el-col :span="12">
                  <input hidden class="input" v-model="form.assets" placeholder="" />
                  <el-tag
                    v-for="asset in form.assetArr"
                    :key="asset.assetId"
                    type="info"
                    effect="plain"
                  >{{asset.assetName}}</el-tag>
                  <el-button size="small" @click="isModal = true">+ </el-button>
                </el-col>
              </el-row>
            </el-form-item>
            <el-form-item label="">
              <el-row :gutter="12">
                <el-col :span="12">
                  <input hidden class="input" v-model="form.bugs" placeholder="" />
                  <el-tag
                    v-for="bug in form.bugArr"
                    :key="bug.bugId"
                    type="info"
                    effect="plain"
                  >{{bug.bugName}}</el-tag>
                  <el-button size="small" @click="isModalBug = true">+ </el-button>
                </el-col>
              </el-row>
            </el-form-item>

            <el-form-item label="">
              <el-row :gutter="12">
                <el-col :span="12">
                  <el-select v-model="form.sacnType" clearable>
                    <el-option value="1" label=""></el-option>
                    <el-option value="2" label=""></el-option>
                    <el-option value="3" label=""></el-option>
                  </el-select>
                </el-col>
              </el-row>
            </el-form-item>

            <el-form-item label="">
              <el-row :gutter="12">
                <el-col :span="12">
                  <el-select v-model="form.reportTemplate" clearable>
                    <el-option value="0" label=""></el-option>
                    <el-option value="1" label="1"></el-option>
                    <el-option value="2" label="2"></el-option>
                    <el-option value="3" label="3"></el-option>
                  </el-select>
                </el-col>
              </el-row>
            </el-form-item>

            <el-form-item label="">
              <el-row :gutter="12">
                <el-col :span="12">
                  <el-input type="textarea" :rows="1" v-model="form.desc" placeholder=""></el-input>
                </el-col>
              </el-row>
            </el-form-item>

            <el-form-item>
              <el-button type="primary" icon="el-icon-plus" @click="createTask"></el-button>
              <el-button icon="el-icon-refresh" @click="form = defaultForm"></el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </div>

      <div class="col-6">
        <el-card shadow="never" style="height: 600px;">
          <div slot="header" class="clearfix">
            <span></span>
          </div>
          <ul class="list">
            <template v-for="(hisTask, index) in hisTaskList">
              <li :key="index">{{ hisTask.createTime }}&nbsp;&nbsp;{{ hisTask.taskName }}</li>
            </template>
          </ul>
        </el-card>
      </div>
    </div>

    <el-dialog title=""  width="70%" :visible.sync="isModal">
      <baseTable :isToolbar="false" ref="assetModal" :fields="fields" baseURL="/api/asset"></baseTable>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="assetModalClick"></el-button>
        <el-button type="primary" @click="isModal = false"></el-button>
      </div>
    </el-dialog>

    <el-dialog title="" width="70%" :visible.sync="isModalBug">
      <baseTable :isToolbar="false" ref="bugModal" :fields="bugFileds" baseURL="/api/bug"></baseTable>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="bugModalClick"></el-button>
        <el-button type="primary" @click="isModalBug = false"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
var df = require('date-formatter')
export default {
  name: 'taskCreate',
  data () {
    return {

      defaultForm: {
        taskName: '',
        bugs: '',
        bugArr: [],
        assets: '',
        assetArr: [],
        cycle: '0',
        sacnType: '1',
        reportTemplate: '0',
        desc: ''
      },

      form: {
        taskName: '',
        bugs: '',
        bugArr: [],
        assets: '',
        assetArr: [],
        cycle: '0',
        sacnType: '1',
        reportTemplate: '0',
        desc: ''
      },

      isModal: false,

      isModalBug: false,

      fields: [
        {
          field: 'assetId',
          name: 'ID',
          hidden: true
        },
        {
          field: 'assetName',
          name: ''
        },
        {
          field: 'updateTime',
          name: '',
          type: 'date'
        },
        {
          field: 'createUserId',
          name: ''
        },
        {
          field: 'createTime',
          name: '',
          type: 'date'
        },
        {
          field: 'updateUserId',
          name: ''
        },
        {
          field: 'remark',
          name: ''
        }
      ],

      bugFileds: [
        {
          field: 'bugId',
          name: 'ID',
          hidden: true
        },
        {
          field: 'bugName',
          name: ''
        },
        {
          field: 'desc',
          name: '',
          type: 'textarea'
        },
        {
          field: 'createTime',
          name: '',
          type: 'date'
        },
        {
          field: 'createUserName',
          name: ''
        },
        {
          field: 'updateTime',
          name: '',
          type: 'date'
        },
        {
          field: 'updateUserName',
          name: ''
        },
        {
          field: 'remark',
          name: '',
          type: 'textarea'
        }
      ],

      hisTaskList: []

    }
  },

  methods: {
    createTask () {
      this
        .$post('/api/task/execCreateTask', this.form)
        .then(data => {
          if (data.success) {
            this.$confirm('?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'success'
            }).then(() => {
              this.$router.push({
                path: '/task/task-table'
              })
            }).catch(() => {
              this.$message({
                type: 'info',
                message: ''
              })
            })

            this.getHisTaskList()
          } else {
            this.$message({
              type: 'error',
              message: data.msg
            })
          }
        })
        .catch(() => {
        })
    },

    assetModalClick () {
      const assetModal = this.$refs.assetModal
      const selectedRowArr = assetModal.getSelectedRows()
      this.form.assetArr = selectedRowArr
      const resultArr = []
      for (let i = 0; i < selectedRowArr.length; i++) {
        resultArr.push(selectedRowArr[i].assetId)
      }
      this.form.assets = resultArr.join(',')
      this.isModal = false
    },

    bugModalClick () {
      const bugModal = this.$refs.bugModal
      const selectedRowArr = bugModal.getSelectedRows()
      const resultArr = []
      for (let i = 0; i < selectedRowArr.length; i++) {
        resultArr.push(selectedRowArr[i].bugId)
      }
      this.form.bugs = resultArr.join(',')
      this.form.bugArr = selectedRowArr
      this.isModalBug = false
    },

    generateTaskName () {
      const date = new Date()
      const dateStr = df(date, 'YYYY-MM-DD hh:mm:ss')
      const taskName = '[' + dateStr + ']'
      this.form.taskName = taskName
      this.form.desc = taskName
    },

    getHisTaskList () {
      this.$post('/api/task/queryPage', { page: { rows: -1, page: 1 } }).then(data => {
        if (data.rows != null) {
          this.hisTaskList = data.rows
        }
      }).catch(e => {
        console.log(e)
      })
    }
  },

  created () {
    this.getHisTaskList()
  }
}
</script>

<style lang="scss" scoped>
.edit-btn {
  display: inline-block;
  margin-left: 8px;
  color: #3080fe;
  cursor: pointer;

  &:hover {
    text-decoration: underline;
  }

  i {
    font-size: 9pt;
  }
}

.el-tag + .el-tag {
  margin-left: 8px;
  margin-top: 8px;
}
</style>



