<template>
  <baseTable :fields="fields" baseURL="/api/log"></baseTable>
</template>

<script>
export default {
  name: 'logTable',

  data () {
    return {

      fields: [
        {
          field: 'logId',
          name: 'ID',
          hidden: true
        },
        {
          field: 'logType',
          name: ''
        },
        {
          field: 'logSource',
          name: ''
        },
        {
          field: 'logContent',
          name: ''
        },
        {
          field: 'createTime',
          name: '',
          type: 'date'
        }
      ]
    }
  }
}

</script>
<style scoped>
</style>



