<template>
  <div>
    <div class="row">
      <div class="col-9">
        <div class="panel" style="height: 250px;">
          <h1 class="panel__title--border"></h1>
          <div class="panel__body">
            <table class="table">
              <tbody>
                <tr>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                </tr>
                <tr>
                  <td></td>
                  <td>100</td>
                  <td></td>
                  <td>10</td>
                </tr>
                <tr>
                  <td></td>
                  <td>1</td>
                  <td></td>
                  <td>15</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>

      <div class="col-3">
        <div class="panel" style="height: 250px;">
          <h1 class="panel__title--border"></h1>
          <div class="panel__body">
            <ul class="list">
              <li>10</li>
              <li>10</li>
              <li>10</li>
            </ul>
            <br>
          </div>
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-12">
        <div class="panel">
          <h1 class="panel__title--border"></h1>
          <div class="panel__body">
            <baseTable :fields="bugTable.fields" baseURL="/api/bug" :isSearch="false" :isToolbar="false"></baseTable>
          </div>
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-12">
        <div class="panel">
          <h1 class="panel__title--border"></h1>
          <div class="panel__body">
            <baseTable :fields="bugTable.fields" baseURL="/api/bug" :isSearch="false" :isToolbar="false"></baseTable>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      bugTable: {
        fields: [
          {
            field: 'bugId',
            name: 'ID',
            hidden: true
          },
          {
            field: 'bugName',
            name: ''
          },
          {
            field: 'desc',
            name: '',
            type: 'textarea'
          },
          {
            field: 'createTime',
            name: '',
            type: 'date'
          },
          {
            field: 'createUserName',
            name: ''
          },
          {
            field: 'updateTime',
            name: '',
            type: 'date'
          },
          {
            field: 'updateUserName',
            name: ''
          },
          {
            field: 'remark',
            name: '',
            type: 'textarea'
          }
        ]
      }
    }
  },

  methods: {}
}

</script>
<style lang='scss' scoped>
// .table tr th, .table tr td {
//   border: 0px;
// }
</style>



